<template>
  <div class="form-examples">
    <typing-title :title="$route.meta.title"></typing-title>

    <div class="z-flex align-center">
      <z-btn
        class="mr-4"
        color="primary"
        lockedTime="1000"
        :unlocked="true"
        @click="getForms"
      >forms</z-btn>

      <v-btn-toggle
        color="primary"
        dense
        mandatory
        @change="changeForm"
      >
        <v-btn>
          1
        </v-btn>
        <v-btn>
          2
        </v-btn>
      </v-btn-toggle>
    </div>

    <v-row justify="center">
      <v-col cols="6">
        <v-card>
          <v-card-text>
            <z-text-field
              :formId="formId"
              formKey="name"
              label="姓名"
              :validation="[
                { rule: 'required', message: '姓名为必填项.' },
                { rule: 'alpha_num', message: '字段只能包含英文字母及自然数.' },
                { rule: 'between:8,16', message: '字段长度在8-16位.' },
              ]"
              defaultValue="Leslie"
            >
              <span class="error--text" slot="prepend-outer">*</span>
            </z-text-field>

            <z-text-field
              v-if="formLayout === 0"
              :formId="formId"
              formKey="phone"
              label="手机"
              :validation="[
                { rule: 'required', message: '姓名为必填项.' }
              ]"
            >
              <span class="error--text" slot="prepend-outer">*</span>
            </z-text-field>
          </v-card-text>

          <v-card-actions>
            <v-spacer></v-spacer>
            <z-btn 
              :formId="formId"
              color="primary"
              btnType="validate"
              lockedTime="0"
              @click="onSubmit"
            >
            <v-icon>mdi-send</v-icon>
            提交</z-btn>

            <z-btn 
              class="mx-2"
              :formId="formId"
              color="warning"
              btnType="reset"
              @click="onReset"
            >
            <v-icon>mdi-reload</v-icon>
            重置</z-btn>

            <z-btn 
              :formId="formId"
              color="error"
              btnType="clear"
              @click="onClear"
            >
            <v-icon>mdi-broom</v-icon>
            清空</z-btn>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
  </div>
</template>

<script>
  import { mapState } from 'vuex'
  import cells from './cells'

  export default {
    name: 'FormExamples',

    data() {
      return {
        formId: 'Examples',
        searchFormId: 'ExamplesSearch',
        cells,
        formLayout: 0
      }
    },

    methods: {
      getForms() {
        console.log(this.forms)
      },

      changeForm(value) {
        this.formLayout = value
      },

      onSearch() {
        console.log(this.forms[this.searchFormId])
      },

      onOpen() {
        this.$refs.dynamic.open()
      },

      onSubmit() {
        console.log(this.forms[this.formId])
      },

      onReset() {
        console.log(this.forms[this.formId])
      },

      onClear() {
        console.log(this.forms[this.formId])
      }
    },

    computed: {
      ...mapState(['forms'])
    },
  }
</script>

<style scoped lang="scss">
  .field {
    padding-bottom: 4px;
  }
</style>